	module.exports = {"data":{"markdownRemark":{"html":"<h2>Anatomy of Letterforms</h2>\n<p>The anatomy of Letterforms describes the different elements that make up printed letters in a typeface. The figure below shows the different parts of the letters in a typeface.</p>\n<p><img src=\"https://upload.wikimedia.org/wikipedia/commons/a/a3/Typographia.svg\" alt=\"alt text\"></p>\n<blockquote>\n<p>Parts of a glyph: 1) x-height; 2) ascender line; 3) apex; 4) baseline; 5) ascender; 6) crossbar; 7) stem; 8) serif; 9) leg; 10) bowl; 11) counter; 12) collar; 13) loop; 14) ear; 15) tie; 16) horizontal bar; 17) arm; 18) vertical bar; 19) cap height; 20) descender line.</p>\n</blockquote>\n<p>A typeface broadly consist of 3 different parts 1) <strong>Strokes</strong> 2) <strong>Terminals</strong> 3) <strong>Space</strong></p>\n<h4>Strokes</h4>\n<p>Stroke refers to the main body of the letterform. They may be straight, as in letters like <em>l</em>, <em>z</em>, <em>k</em>, <em>v</em> or curved like in <em>c</em> or <em>o</em>. The different parts of the stroke are given below</p>\n<ul>\n<li>\n<p>The imaginary line on which most characters sit is known as the <strong>Baseline</strong> (4).</p>\n</li>\n<li>\n<p>The capheight or <strong>capline</strong> (19) is another imaginary line depicting the height of all the uppercase alphabets.</p>\n</li>\n<li>\n<p>The imaginary horizontal line that marks the top edge of the lowercase letters is the <strong>Meanline</strong>.</p>\n</li>\n<li>\n<p>The <strong>x-height</strong> (1) is the height of the lowercase x. In other words it is the distance between the baseline and the meanline.</p>\n</li>\n<li>\n<p>The main vertical or diagonal stroke in a letterform is known as the <strong>Stem</strong> (7).</p>\n</li>\n<li>\n<p>The strokes that connect two lines in the capital letterforms of <em>A</em> and <em>H</em> or cross strokes in <em>t</em> is known as <strong>Crossbar</strong> (6).</p>\n</li>\n<li>\n<p>When the stroke of a lowercase letter rises above the meanline like in <em>l</em> it is called an <strong>ascender</strong> (5). The <strong>Ascender line</strong> (2) is the imaginary line depicting the distance between the baseline and the top of the ascender.</p>\n</li>\n<li>\n<p>When the stroke of a lowercase letter falls below the baseline like in <em>g</em> it is called a <strong>descender</strong>. The <strong>Descender line</strong> (20) is the imaginary line depicting the distance between the baseline and the bottom of the descender.</p>\n</li>\n<li>\n<p>An arching stroke as in the top of an <em>R</em> is called a <strong>Shoulder</strong> or sometimes just an <em>arch</em>, as in <em>h</em>, <em>n</em>, <em>m</em>.</p>\n</li>\n<li>\n<p>A closed curved stroke is called a <strong>Bowl</strong> (10).</p>\n</li>\n<li>\n<p>The inferior diagonal stroke seen in <em>K</em> and <em>R</em> is called <strong>Leg</strong> (9).</p>\n</li>\n<li>\n<p>A short horizontal stroke seen in the center of <em>e</em>, <em>f</em> and in the middle stroke of <em>E</em> and <em>F</em>, is called a <strong>Bar</strong>.</p>\n</li>\n<li>\n<p>The longer horizontal Stroke on the top or bottom as seen in <em>E</em> and <em>F</em> is called the <strong>Arm</strong> (17).</p>\n</li>\n<li>\n<p>The dot above the lowercase <em>j</em> and <em>i</em> is called the <strong>Tittle</strong> or dot or a jot.</p>\n</li>\n<li>\n<p>The bottom part of the two-story <em>g</em> is called a <strong>Loop</strong> (13).</p>\n</li>\n</ul>\n<h4>Terminals</h4>\n<p>Terminals are the end of the strokes. They are of two types <strong>Serif</strong> and <strong>Sans-Serif</strong>. A seriffed terminal has proftusions on the edges which can be described as a wedge, bulbous, teardrop, slab, etc. Sans-Serif on the other hand does not have these extending features at the end of strokes. Sans-Serif is used often on digital displays these days since they have better legibility on lower resolution displays where serifs are hard to display.</p>\n<h4>Space</h4>\n<p>Space refers to the white space that is found between the letters and also inside letters like <em>o</em> and <em>p</em> with closed loops.</p>\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<ul>\n<li>Wikipedia Article <a href=\"https://en.wikipedia.org/wiki/Typeface_anatomy\">Link</a></li>\n<li>Sitepoint <a href=\"https://www.sitepoint.com/the-anatomy-of-a-letterform/\">Link</a></li>\n</ul>","fields":{"slug":"/typography/anatomy-of-letterforms/"},"frontmatter":{"title":"Anatomy of Letterforms"}}},"pathContext":{"slug":"/typography/anatomy-of-letterforms/"}}

